import React,{ Component }  from 'react';
import { Button, Card  } from 'antd';
import '../../App.css';
import web3 from "../../ABI/web3";
import airdrop from '../../ABI/airdrop';
import LG from '../../ABI/LG-ERC20';
import airdropEveryDay from '../../ABI/airdropEveryDay';
import { Link } from 'react-router-dom';
import { SmileOutlined } from '@ant-design/icons';

class Wakuang extends Component {
    state = {
        AirdropAmount:"0",
        airdropadr:"0x6ce2f1206a8b6736543e357049a7e17f0cb29368",  //LG空投合约地址
        airdropEveryDay_adr:"0xA0284950Db6b425e9314Fa3b6BC1aA8027E8d2A4",  //每日空投合约地址
        AirdropAmountEveryDay:"0",
        time:"",
        message:"",
        disabled:'',
        yiban:'none',  
      };
    
      async componentDidMount(){
        //判断页面是否安装了Metamask
        if(typeof window.ethereum !== 'undefined'){
          const ethereum = window.ethereum
          //禁止页面自动刷新，metamask要求写的
          ethereum.autoRefreshOnNetwoorkChange = false
          try{
            //第一次链接Metamask
            const accounts = await ethereum.enable()
        
            //初始化Provider
            const provider = window['ethereum']
            
            if(provider.chainId == 0x1){this.setState({chainId:"以太链"});
              this.setState({disabled:"disabled"})};
      
            //获取用户帐号
            window.defaultAccount = accounts[0].toLowerCase()

            //切换Metamask帐号
            ethereum.on('accountsChanged', function (accounts){
              console.log("accountsChanged:" + accounts)
            })
      
            //切换Metamaskm网络ID
            ethereum.on(function (networkVersion){
              console.log(networkVersion)
            })
      
          }catch(e){}
        }else{console.log('没有metamask')}
      
      
        //获取用户帐号中的LG余额
        const LGbalance = await LG.methods.balanceOf(window.defaultAccount).call();
        const a = (LGbalance/10**18).toFixed(2)
        this.setState({LGbalance:a});
      
        //获取空投池LG余额
        const Airdrop_LGbalance = await LG.methods.balanceOf(this.state.airdropadr).call();
        this.setState({Airdrop_LGbalance});
      
       //获取每日一领池LG余额
        const AirdropEveryDay_LGbalance = await LG.methods.balanceOf(this.state.airdropEveryDay_adr).call();
        this.setState({AirdropEveryDay_LGbalance});
      
        //获取空投量
        const AirdropAmount = await airdrop.methods.AirdropAmount().call();
        if(AirdropAmount/10**18 <= Airdrop_LGbalance/10**18){
        this.setState({AirdropAmount})}
      
      

        //获取每日一领数量
        const AirdropAmountEveryDay = await airdropEveryDay.methods.AirdropAmount().call();
        if(AirdropAmountEveryDay/10**18 <= AirdropEveryDay_LGbalance/10**18){
        this.setState({AirdropAmountEveryDay})}
      
        //获取领取频率
        const time = await airdropEveryDay.methods.time().call();
        this.setState({time});
      
        }
    
        getAirdrop =async () => {
          this.setState({message:"等待打包……"});
          await airdrop.methods.getAirdrop().send({from:window.defaultAccount});
          this.setState({message:"领取成功请回钱包首页查看"});
        }
        
        getAirdropEveryDay = async () =>{
          this.setState({message:"等待打包……"});
          await airdropEveryDay.methods.getAirdrop().send({from:window.defaultAccount});
          this.setState({message:"领取成功请回钱包首页查看"});
        }
    
  render(){
    let yiban = this.state.yiban;
  return (
        
        <Card  type="inner" title="LG空投" extra={<Link to="/warning">More</Link>} >
            <p>新人福利：{this.state.AirdropAmount/10**18}
            <span style={{float:'right'}}>
                <Button onClick={() => {this.getAirdrop()}} 
                    disabled={this.state.disabled} 
                    type="primary"  
                    size='small'>
                    领取
                </Button>
            </span>
            </p>
            <p>{this.state.time}天1领见证成长:max
                {web3.utils.fromWei(this.state.AirdropAmountEveryDay,"ether")*2*0.9}
                <span style={{float:'right'}}>
                    <Button onClick={() => {this.getAirdropEveryDay()}}
                        disabled={this.state.disabled} 
                        type="primary"  
                        size='small'>
                        领取
                    </Button>
                </span>
            </p>
            {/* <span style={{display:yiban}}>
                <Result icon={<SmileOutlined />} title="手气一般明天再来哦"
                extra={<Button type="primary"><Link to="/">OK</Link></Button>}
                />
            </span> */}
            <div style={{color:"green",fontSize:"12px"}}>
                  {this.state.message}
          </div>
        </Card>
                
      
  );
}
}

export default Wakuang;
